@import url('https://fonts.googleapis.com/css2?family=Arizonia&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap');
.ul{
    list-style: none;
    background-color: #07020B;
}

.li{
    font-family: 'Poppins',sans-serif;
    color: white;
}
.portfolio__navbar-container__links{
    display: flex;
    padding: 5rem 0;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    height: 60vh;

}

.portfolio__navbar-container__links p {
    height: 5vh;
    width: 85px;
}

.portfolio__navbar-container__links a{
    color: white;
    text-transform: uppercase;
    font-family: 'Poppins', sans-serif;
    position: relative;
    letter-spacing: 2px;
    font-size: 12px;
    color: #B4A3BC;
    transition: all .7s;
}

.portfolio__navbar-container__links a:hover {
    color: #ff3c78;
    font-size: 18px;
}


.portfolio__navbar-container__links a:after {
    content: "";
    position: absolute;
    background-color: #ff3c78;
    height: 3px;
    width: 0;
    left: 0;
    bottom: -10px;
    transition: all 0.5s;
}

.portfolio__navbar-container__links a:hover:after {
    width: 100%;
    transform: 50% 50%;
}

.portfolio__navbar-logo__name p{
    font-size: 40px;
    padding-top: 50px;
    font-family: 'Arizonia', cursive;
    color: #FEEDB5;
}

.portfolio__navbar__main{
    height: 100%;
    width: 15rem;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #35074A;
    overflow-x: hidden;
}

.portfolio__navbar-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navbar__link__active{
    color: #ff3c78 !important ;
    font-size: 18px !important ;
}

.portfolio__navbar__mobile__container{
    display: none;
}

.portfolio__navbar-logo__namer__mobile{
    display: none;
}

.portfolio__navbar__mobile{
    display: none;
}

@media screen and (max-width: 1080px){
    .portfolio__navbar__main {
            width: 10rem;
            /* display: none; */
        }
    .portfolio__navbar-logo__name p {
        font-size: 30px;
        padding-top: 50px;
    }
}


@media screen and (max-width: 780px){
    .portfolio__navbar__main {
            display: none;
        }
    .portfolio__navbar__mobile{
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        text-align: start;
        background: #353435a1;
        padding: 2rem;
        position: absolute;
        top: 80px;
        left: 40px;
        margin-top: 1rem;
        min-width: 210px;
        border-radius: 5px;
        box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
        transition: all 0.5s ease-in-out;
    }
    .portfolio__navbar-logo__namer__mobile {
        display: block;

    }
    .portfolio__navbar-logo__namer__mobile p {
        font-family: 'Arizonia', cursive;
        color: #FEEDB5;
        font-size: 40px;;
    }
    .portfolio__navbar__mobile__container{
        display: flex;
        width: 30%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        position: absolute;
        top: 40px;
        left: 0;
        min-width: 110px;
        border-radius: 5px;
    }
    .portfolio__navbar-container__links__mobile p {
        height: 5vh;
        width: 85px;
    }

    .portfolio__navbar-container__links__mobile a {
        color: white;
        text-transform: uppercase;
        font-family: 'Poppins', sans-serif;
        position: relative;
        letter-spacing: 2px;
        font-size: 12px;
        color: #B4A3BC;
        transition: all .7s;
        font-weight: 500;
    }

    .portfolio__navbar-container__links__mobile a:hover {
        color: #ff3c78;
        font-size: 15px;
    }


    .portfolio__navbar-container__links__mobile a:after {
        content: "";
        position: absolute;
        background-color: #ff3c78;
        height: 3px;
        width: 0;
        left: 0;
        bottom: -10px;
        transition: all 0.5s;
    }

    .portfolio__navbar-container__links__mobile a:hover:after {
        width: 100%;
        transform: 50% 50%;
    }
    .navbar__link__active {
        color: #ff3c78 !important;
        font-size: 15px !important;
    }
}

@media screen and (max-width: 780px) {
    .portfolio__navbar-logo__namer__mobile p {
        font-size: 30px;
    }
}

@media screen and (max-width: 450px) {
    .portfolio__navbar__mobile__container{
        width: 80%;
        align-items: center;
        justify-content: space-between;
    }
    .portfolio__navbar-logo__namer__mobile p {
        font-size: 19px;
        margin-right: 2rem;
    }
}